@import "variables.scss";

// Rotating Text
.rotating-text {
  color: $white;
  font-family: "Chakra Petch", sans-serif;
  font-size: 48px;
  font-weight: 500;
  // text-align: center;
  padding: 0 20px;

  #text-rotate {
    display: inline;

    & > .cursor {
      animation: blink 1s infinite;
      border-bottom: 4px solid $orange;
      display: inline-block;
      margin-left: 0.25rem;
      width: 1.5rem;
    }
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

// Typewriter Animation
h1.typewriter {
  animation: typing 3.5s steps(30, end), blink-caret 0.8s step-end infinite;
  border-right: 4px solid $orange;
  color: $white;
  display: inline-block;
  font-family: "Chakra Petch", sans-serif;
  overflow: hidden;
  white-space: nowrap;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: $orange;
  }
}

// CSS Wave Animation
.waves {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 15vh;
  min-height: 100px;
  max-height: 150px;
}

.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}

@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}

@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
}

// Floating Embers Animation
#embers {
  position: fixed;
  top: 0;
  right: 20px;
  width: 480px;
  height: 100vh;
  background: transparent;
  z-index: -1;

  #ember_01 {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(../assets/ember_01.png) repeat-y;
  }
  #ember_02 {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(../assets/ember_02.png) repeat-y;
  }
  #ember_03 {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    margin-left: -180px;
    background: url(../assets/ember_03.png) repeat-y;
  }
}
